import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { BluetoothSerial } from '@ionic-native/bluetooth-serial';

/**
 * Generated class for the SincronizarBluetoothPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-sincronizar-bluetooth',
  templateUrl: 'sincronizar-bluetooth.html',
})
export class SincronizarBluetoothPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, private bluetoothSerial: BluetoothSerial) {
  }

  habilitado(){
    if(this.bluetoothSerial.isEnabled())
      return true;
    else 
      return false;
  }
}



